import React, { useState } from 'react';
import { Alert, Button } from 'react-bootstrap';
import '../styles/Alert.css'; //   CSS  

const DataCollectionAlert = () => {
    const [show, setShow] = useState(true);
    const handleClose = () => {
        setShow(false);
        storeDataInLocalStorage();
    };
    const storeDataInLocalStorage = () => {
        localStorage.setItem('dataCollection', '1');
    };
    return (
        <div>
            {show && (
                <Alert
                    variant="info"
                    dismissible
                    onClose={handleClose}
                    className="alert-slide-in myAlert"
                >
                    <Alert.Heading>
                           
                    </Alert.Heading>
                    <p>
                                       (localStorage).
                               . ,   .
                    </p>
                    <div className="d-flex justify-content-end">
                        <Button onClick={handleClose} variant="outline-secondary">
                            
                        </Button>
                    </div>
                </Alert>
            )}
        </div>
    );
};

export default DataCollectionAlert;
